.prices{
margin-top: 40px;
}
.prices-title{
  font-weight: 700;
  font-size: 40px;
  line-height: 50px;
  display: flex;
  justify-content: space-between;
  @include phone{
    flex-direction: column;
    align-items: center;
  }
}
.prices-title span{
  color: #499A18;
}
.prices-title h2{
  margin-right: 50px;
}
.two-h5{
  max-width: 456px;
  max-height: 104px;
  text-align: right;
  @include phone{
    text-align: center;
  }
}
.cat-prices{
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 230px;
  border: 1px solid #E3E1D5;
border-radius: 20px;
align-items: center;
text-align: left;
margin-bottom: 100px;
justify-content: space-between;


}
.unactive{
  margin-top: 15px;
  background-color:#EDF2EC;
  width: 260px;
  height: 50px;
border-radius: 20px;
padding-top: 5px;
text-align: center;
padding-top: 15px;
font-weight: 700;
font-size: 20px;
border: 1px solid rgb(70, 70, 70, 0.3);


&:hover{
  background-color: #D6E7D2;
}
}

.prices-flex{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include phone{
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
    margin-bottom: 30px;
  }

}
.btn-prices{
  font-weight: 700;
font-size: 20px;
line-height: 26px;
color: #E06733;
border: 1px solid #E06733;
border-radius: 5px;
font-family: "Inika";
padding: 12px 32px;

&:hover{
  background-color: #E06733;
  color: white;

}
}
.listik{
  display: flex;
  flex-direction: column;
}
.listik img{
  margin-top: 40px;
  @include phone{
    display: none;
  }
}
.pro-care{
  margin-bottom: 15px;
}
.lop3{
  cursor: pointer;
  @include phone{
    margin-bottom: 130px;
  }
}
.basic{
  cursor: pointer;
}
.standart{
  cursor: pointer;
}
.pro-care{
  cursor: pointer;
}
