.contact{
  min-height: 542px;
  background-color: var(--color-background);
}
.contact h3{
  padding-top: 20px;
  text-align: center;
  font-family: 'Inika';
  font-weight: 700;
  font-size: 40px;
  line-height: 52px;
  color: #499A18;
  margin-left:45px;
  @include tablet{
    margin: 0;
    margin-top: 30px;
  }
}
.woman{
  display: flex;
  justify-content: space-between;
  @include tablet{
    flex-direction: column;
    .woman {display: flex;flex-flow: wrap;}
  .col1, .col2, .col3
  .col1 {order: 4;}
  .col2 {order: 2;}
  .col3 {order: 1;}
  .col4 {order: 2;}
  align-items: center;
  }
}
.woman-img{
  padding-top: 45px;
  @include tablet{
    width: 200px;
    height: 238px;
    padding: 0;
    margin-top: 250px;
  }
}


.contact-city{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 150px;

  @include phone{
    margin-top: 100px;
  }
}
.city-list{
  margin-top: 15px;
  font-weight: 400;
font-size: 16px;
line-height: 40px;
letter-spacing: 0.05em;
border-bottom: 1px solid gray;
color: #499A18;

&:last-child{
  margin-bottom: 20px;
}
}
.city-ul{
  background-color:#DCE9D9;
  width: 450px;
  text-align: left;
  padding: 0 30px;
  border-radius: 0 0 5px 5px;
  margin-top: 50px;
  position: absolute;
  box-shadow: 0px 7px 7px rgba(0, 0, 0, 0.50);
  z-index: 100;
  overflow: hidden;
  @include phone{
    width: 270px;
  }
}
.city-ul-none{
  display: none;

}

.button-active{
  height: 50px;
  width: 450px;
  background-color: #C1E698;
  border: 0;
box-shadow: 0px 7px 7px rgba(0, 0, 0, 0.50);
border-radius: 5px 5px 0 0;
font-weight: 400;
font-size: 20px;
line-height: 40px;
letter-spacing: 0.05em;
color: #717171;
transition: 0.15s ease;
@include tablet{
  margin: 0;
}
@include phone{
  width: 270px;
}
}

.button-unactive{
  height: 50px;
  width: 450px;
  background-color: #D6E7D2;
  border: 0;
box-shadow: 0px 7px 7px rgba(0, 0, 0, 0.50);
border-radius: 5px;
font-weight: 400;
font-size: 20px;
line-height: 40px;
letter-spacing: 0.05em;
color: #717171;
transition: 0.15s ease;
@include tablet{
  margin: 0;
}
@include phone{
  width: 270px;
}
}

.button-img-unactive{
  padding: 0;
  width: 16px;
  height: 16px;
  margin: 0;
  margin-left: 10px;
  transition: 0.4s ease-in-out;
}
.button-img-active{
  padding: 0;
  width: 16px;
  height: 16px;
  margin: 0;
  margin-left: 10px;
  color: #8BA07E;
  transform: rotate(180deg);
  transition: 0.4s ease-in-out;
}
.col3{
  cursor: pointer;
}
.city-list{
  cursor: pointer;
}
