/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}
@mixin hign-desktop {
  @media (max-width: (1200px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
@mixin phone {
  @media (max-width: (650px)) {
    @content;
  }
}

@mixin min-tablet {
  @media (max-width: (810px)) {
    @content;
  }
}

@mixin max-phone {
  @media (max-width: (611px)) {
    @content;
  }
}
@mixin phone-450 {
  @media (max-width: (450px)) {
    @content;
  }
}
