.footer{
  min-height: 130px;
}
.footer-flex{
  padding: 40px 0;
  @include flex;
  justify-content: space-between;
  font-weight: 400;
font-size: 20px;
line-height: 50px;
@include phone{
  flex-direction: column;
  align-items: center;
}
}
.footer-flex-massanger{
  @include flex;
  :hover{
    fill: #E06733;
  }
}
.footer-flex-massanger svg{
width: 32px;
height: 32px;
  transition: all 0.5s;


}
.footer-flex-massanger li{
  &:not(:last-child){
    margin-right: 20px;
  }
}
.footer-flex a:visited{
 color: black;
}
.footer-flex a{
  color: black;
 }
.footer-git{
  word-spacing: 15px;
}
.footer-school{
  margin-left: 20px;
}
.footer-flex a{
  @include phone{
    margin: 0;
    &:not(:last-child){
      margin-bottom: 12px;
    }
  }
}
