.burger{
  display: none;
  @include phone{
    display: unset;
    width: 40px;
height: 24px;
  }

}
.burger__line{

  @include phone{
    position: absolute;
    left: 85%;
    top: 7%;
    transform: translateX(-50%) translateY(-50%);
    background-color: #717171;
    border-radius: 100px;
    height: 2px;
    width: 40px;

    &:first-child{
      margin-top: -8px;
    }
    &:last-child{
      margin-top: 8px;

  }

}
}

.burger-close{
  display: none;
  @include phone{
    display: unset;
  }
  position: absolute;
  right: var(--container-offset);
  width: 40px;
  height: 40px;
  background-image: url('../img/svg/exit.svg');
  background-position: center;
  background-size: 35px 35px;
  background-repeat: no-repeat;
}
