.contact-adress{
    width: 450px;
    height: 215px;
    position: absolute;
    background: #EDF2EC;
    border: 1px solid #000000;
    border-radius: 20px;
    margin-top: 120px;
    z-index: 10;
    padding: 18px 36px 32px 40px;
    overflow: hidden;
    @include phone{
        margin-top: 100px;
        width: 290px;
        height: 220px;
        padding: 13px 16px 12px 20px;
    }

}

.contact-adress-title{
    display: flex;
    flex-direction: column;

    h2{
        font-weight: 400;
font-size: 17px;
line-height: 40px;
letter-spacing: 0.05em;
color: #000000;
font-family: 'Inter';
margin: 5px 0;
@include phone{
        &:last-child{
            line-height: 20px;
            }
    }
    }

}
.contact-adress-btn{
width: 122px;
height: 26px;
border: 1px solid #E06733;
border-radius: 5px;
font-weight: 700;
font-size: 10px;
line-height: 13px;
text-align: center;
color: #717171;
margin: 0 auto;
display: block;
@include phone{
    margin-top: 10px;
}
}
.contact-adress-descr{
    display: flex;
        flex-direction: column;
        height: 150px;
        margin-left: 25px;
        @include phone{
            margin-left: 5px;
            height: 150px;
        }
        :nth-child(2){
            margin-left: -2px;
        }
    a{

        text-align: right;
        font-weight: 400;
       font-size: 14px;
       line-height: 40px;
       letter-spacing: 0.05em;
       color: #717171;
       text-align: left;
       margin: 5px 0;
       @include phone{
        &:last-child{
            line-height: 20px;
            }
    }
           }
}
.contact-adress-block{
    display: flex;
    align-items: center;
}
.city-list:hover{
    color: gray;
}
.color-gray{
    color: black;
    border-color: #fa0000;
}
