.gardeners{
  min-height: 500px;
  text-align: center;
}
.gardener-block{
  margin-top: 93px;
  display: flex;
  justify-content: space-between;
  @include tablet{
    flex-direction: column;
    align-items: center;
  }
}
.gardener-more__title{
  max-width: 463px;
  margin-top: 55;
  font-weight: 400;
  font-size: 40px;
  line-height: 52px;
  text-align: left;
  @include phone{
    font-size: 28px;
    text-align: center;
  }


}
.gardener-more__descr{
  max-width: 605px;
  font-weight: 400;
 font-size: 20px;
 line-height: 30px;
 color: #636060;
 text-align: left;
 @include phone{
  font-size: 15px;
  text-align: center;
}
@include tablet{
  text-align: center;
  align-items: center;
}

}
.gardener-more__title span{
  color: #499A18;
}
.gardener-more__title{
  color: #E06733;
}
.gardener{
  @include tablet{
    width: 250px;
    height: 258px;
  }
}
.gardener-more{
  @include tablet{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
