:root {
  --font-family: "Inter", sans-serif;
  --content-width: 1222px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-white: #fff;
  --color-background: #EDF2EC;
}

