.service{
  min-height: 1300px;
  background-color: var(--color-background);
  margin-top: 94px;
  @include tablet{
    padding-bottom: 70px;
  }
}
.service__title{
  font-family: 'Inika';
  max-width: 306px;
padding-top: 40px;
font-weight: 400;
font-size: 40px;
line-height: 52px;
color: #499A18;
margin: 0;
}
.service-descr{
  display: flex;
  padding-bottom: 90px;
  @include tablet{
    flex-direction: column;
  }
}
.service-btn{
padding: 0;
height: 50px;
width: 161px;
background-color: var(--color-background);
border: 1px solid #E06733;
color: #E06733;
font-weight: 400;
font-size: 20px;
line-height: 26px;
border-radius: 5px;
cursor: pointer;
@include phone-450{
  margin-top: 20px;
}



&:hover{
  background-color: #E06733;
  transition: all 0.4s;
  color: white;

}
&:not(:last-child){
  margin-right: 40px;
  @include phone-450{
    &:not(:first-child){
      margin-top: 20px;
    }
    margin-right: 0;
  }
}
}
.service-group-btn{
padding-top: 69px;
@include phone-450{
  display: flex;
  flex-direction: column;
  margin: 0;
  align-items: center;
}
}

.first-btn{
  margin-left: 142px;
  @include tablet{
    margin: 0;
    }
    @include max-phone{
      margin-bottom: 25px;
    }
    @include phone-450{
      margin-bottom: 0;
    }

}

.photo-one-link{
display: grid;
grid-template-columns: repeat(9, 1fr);
gap: 55px 116px;
@include tablet{
  grid-template-columns: repeat(2, 1fr);
  gap: 38px 106px;
  justify-items: center;
}
@include phone{
display: flex;
flex-direction: column;
align-items: center;
}
@include min-tablet{
  display: flex;
  flex-direction: column;
  align-items: center;

  }
}



.img-a{
  color:#E06733;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  padding: 10px 0;
  text-align: center;
  height: 100px;
  border: 1px solid #E3E1D5;
border-radius: 0px 0px 20px 20px;
height: 100px;
width: 330px;
padding: 0 15px;
padding-top: 20px;
}

.flex-a img{
  min-width: 330px;
}

.img-p{
  font-weight: 400;
font-size: 16px;
line-height: 20px;
color: #717171;

}


.service img{
  height: 350px;
  width: 330px;
}
 .flex-a{
  display: flex;
  flex-direction: column;
  grid-column: 3 span;
  align-items: center;
  transition: 0.5s ease-in-out;
  border-radius: 20px;
  @include tablet{
    grid-column: 1 span;
  }
 }
.blur{
  filter: blur(11px);
  transition: 0.5s ease-in-out;
}
.shadow{
  box-shadow: 0px 7px 7px rgba(0, 0, 0, 0.50);

  transition: 0.5s ease-in-out;
}

.bgcolor{
  background-color: #E06733;
  color: white;
  box-shadow: 0px 7px 7px rgba(0, 0, 0, 0.50);
  transition: 0.5s ease-in-out;
}
