.more{
  background-color: var(--color-background);
  min-height: 534px;
  padding-bottom: 50px;


  @include phone{
    min-height: 655px;
    text-align: center;
  }
  @include small-tablet{
    min-height: 640px;
  }

}

.more-block{
  display: flex;
  justify-content: space-between;
  @include tablet{
    display: flex;
    .woman {display: flex;flex-flow: wrap;}
    .lop1,.lop2
  .lop1 {order: 1;}
  .lop2 {order: 2;}
  }
  @include phone{
   justify-content: center;
  }
}


.read-more{
  max-width: 549px;
  min-height: 340px;
  @include phone{
    z-index: 10;
  width: 375px;
  }
}
.read-more__title{
  font-weight: 700;
  font-size: 45px;
  line-height: 50px;
  color: #717171;
  margin-bottom: 42px;
  @include phone{
   color: black;

  }
}
.read-more__descr{
  padding-right: 15px;
  font-weight: 400;
  font-size: 20px;
  line-height: 35px;
  color: #636060;

  @include phone{
    font-size: 16px;
    text-align: center;
  }
}
.read-more__title span{
  color: #499A18;
  @include phone{
    color: #398b06;
  }
}
.read-btn{
  border: 2px solid #E06733;
  border-radius: 10px;
  transition: all 0.4s;
  padding: 10px 27px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  background-color: inherit;
  margin-top: 48px;
  color: black;
  margin-bottom: 48px;
  &:hover{
    background-color: #E06733;
  }
}
.lop2{
  height: 450px;
  @include tablet{
    width: 280px;
  height: 254px;
  margin-top: 40px;
  }
}
.more-block img{
  @include phone{
    position: absolute;
    z-index: 0;
  }
}

.more-padding{
  padding-top: 134px;
}
