.header{
background-color: var(--color-background);
padding: 42px 0;
height: 134px;
font-family: "Inika";
}
.nav-li{
  font-weight: 400;
  font-size: 16px;
  line-height: 50px;
  color: #010201;
  &:not(:first-child){
    cursor:pointer ;
  }
  &:first-child{
    cursor: default;
  }
  &:not(:last-child){
    margin-right: 44px;
    @include phone{
      margin-right: 0px;
      margin-bottom: 25px;
     }

  }
  @include phone{
    font-size: 30px;
   }
}
.nav-ul{
  @include flex;
  @include phone{
    @include flex-all-center;
    flex-direction: column;
width: 100%;
height: 100%;

  }
}
.container__header{
  @include flex;
  justify-content: space-between;
}
.nav-li__home{
  color: #E06733;
}

.nav{
  @include phone{
    padding: 0;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--color-background);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    margin-top: -32px;
  }
}

.menu--active{
  padding-top: 0;
  opacity: 1;
  visibility: visible;
}

.upward{
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 50px;
  height: 50px;
  background: url('../img/upward.png');
  background-size: cover;
  cursor: pointer;
  border: 2px solid #E06733;
  border-radius: 25%;
  visibility: hidden;
  opacity: 0;
  z-index: 500;
  transition: 0.5s all;
  @include phone{
    width: 45px;
    height: 45px;
    right: 20px;
    bottom: 20px;
  }
}

.upward.active{
  visibility: visible;
  opacity: 1;
}
